/* SearchBar */

.search-bar-dropdown {
    width: 20em;
    position: relative;
  }
  
  #results {
      z-index: 2;
    position: absolute;
    width: inherit;
    overflow-y: scroll;
    height: 10em;
    /* border-bottom: 1px #eeeeee solid; */
  }
  
  
  #results > button {
    background-color: #fafafa;
  }
  #results:empty{
      display: none !important;
  }
  /* width */
  ::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background-color: #eeeeee;
    border-radius: 10px;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 10px;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.3);
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  
  #search-bar {
    width: inherit;
    height: 3em;
    padding-left: 2em;
  }
  



  /* Add Address  */

  #savebtn:disabled {
    background-color: lightgray;
    color: #b3b3b3;
  }


  .customcolor.MuiListItem-root.Mui-selected{
      border: 2px solid red !important;
  }